<script>
  import axios from 'axios'
  export default {
    data () {
      return {
        username: '',
        password: '',
        age: '',
        sex: '',
        phone: '',
        address: '',
        nickName: '',
        id: '',
        items: [
          {
            title: 'jjp',
            href: 'http://google.com',
            url: 'http://localhost:8088/static/poster01.png'
          },
          {
            title: 'tool',
