import Vue from 'vue'
import Router from 'vue-router'
import QueryUser from '@/components/queryUser'
import Main from '../pages/main.vue'
import Tool from '../pages/tool.vue'
import My from '../pages/my.vue'

Vue.use(Router)

export default new Router({
  routes: [

    {
      path: "/",
      name: 'main.vue',
      component: Main
    },
    {
      path: '/main',
      name: 'main.vue',
      component: Main
    },
    {
      path: '/tool',
      name: 'tool.vue',
      component: Tool
    },
    {
      path: '/my',
      name: 'my.vue',
      component: My
    }/*,
    {
    path: '/queryUser',
    name: 'queryUser.vue',
    component: QueryUser
   }*/

  ]
})



