<template>
    <div>
      <!--<div>{{id}}</div>
      <div>{{username}}</div>
      <div>{{nickName}}</div>
      <div>{{password}}</div>
      <div>{{age}}</div>
      <div>{{sex}}</div>
      <div>{{phone}}</div>
      <div>{{address}}</div>-->

      <el-table
        style="width: 100%">
        <el-table-column
          prop="id"
          label="ID"
          width="80">
        </el-table-column>
        <el-table-column
          prop="username"
          label="用户名"
          width="80">
        </el-table-column>
        <el-table-column
          prop="nickName"
          label="昵称">
        </el-table-column>
        <el-table-column
          prop="password"
          label="密码"
          width="80">
        </el-table-column>
        <el-table-column
          prop="age"
          label="年龄"
          width="80">
        </el-table-column>
        <el-table-column
          prop="sex"
          label="性别">
        </el-table-column>
        <el-table-column
          prop="phone"
          label="电话"
          width="80">
        </el-table-column>
        <el-table-column
          prop="address"
          label="住址"
          width="80">
        </el-table-column>

      </el-table>
      <el-table  style="width: 100%">
        <el-table-column width="80">{{id}}</el-table-column>
        <el-table-column width="80">{{username}}</el-table-column>
        <el-table-column width="80">{{nickName}}</el-table-column>
        <el-table-column width="80">{{password}}</el-table-column>
        <el-table-column width="80">{{age}}</el-table-column>
        <el-table-column width="80">{{sex}}</el-table-column>
        <el-table-column width="80">{{phone}}</el-table-column>
        <el-table-column width="80">{{address}}</el-table-column>
      </el-table>
    </div>
</template>

<script>
  import axios from 'axios'
/*  import ELementUI from 'ELementUI'*/

  export default {
    name: 'queryUser',

    data() {
      return {
        username: '',
        password: '',
        age: '',
        sex: '',
        phone: '',
        address: '',
        nickName: '',
        id: ''

      }
    },
    mounted() {
      //发ajax请求取数据/get/1
      axios.get('/muser/get/1').then(
        //成功
        response => {
          const result = response.data
         // alert('code is: '+result.code+' message is:'+result.message)
          console.log('result is:',result);
          this.username=  result.data.username
          this.password = result.data.password,
          this.age=result.data.age, this.sex=result.data.sex,
          this.phone=result.data.phone, this.address=result.data.address,
          this.nickName=result.data.nickName,
          this.id=result.data.id
        }).catch(error =>{
        alert('请求失败！！！')
      })

   }
  }
</script>

<style scoped>
  .el-table .warning-row {
    background: oldlace;
  }

  .el-table .success-row {
    background: #f0f9eb;
  }
</style>



