<template>

  <div>
    <mt-index-list>
      <mt-index-section v-for='data in CityList' :key='data.index' :index="data.index">
        <mt-cell v-for="item in data.list" :key='item.id' :title="item.name"></mt-cell>
      </mt-index-section>
    </mt-index-list>
  </div>

</template>

<script>
  export default {
    data() {
      return {
  /*      CityList: [
          { index: "A",list:[id:"1",name: "北京",py: 'beijing']},
          { index: "B",list:[id:"2",name: "上海",py: 'shanghai']},
          { index: "C",list:[id:"3",name: "广州",py: 'guangzhou']},
          { index: "D",list:[id:"4",name: "深圳",py: 'shengzhen']}
        ]
*/

      CityList: [
        {
          index: 'A',
          list:
            {
              id: '1',
              name: '北京',
              py: 'beijing'
            }
         },
        {
          index: 'B',
            list:
              {
                id: '2',
                name: '上海',
                py: 'beijing'
              }
       },
        {
          index: 'C',
            list:
              {
                id: '3',
                name: '广州',
                py: 'beijing'
              }
       },
       {
          index: 'D',
            list:
              {
                id: '4',
                name: '深圳',
                py: 'beijing'
              }
        }
      ]

      }

    }
  }
</script>

<style scoped>

</style>



